export default "<template>\r\n  <div>\r\n    <raw-loader :mytxt=\" require('!raw-loader!./KonvaVueLine2.vue').default \"></raw-loader> \r\n    <h1>This is KonvaVue</h1>\r\n    <b-row>\r\n      <b-col sm=\"3\" class=\"border-right\">\r\n        <div class=\"mb-1\">\r\n          <button class=\"mr-1\" :disabled=\" (Sel.length==1) ? false : true \" @click=\"onDel\">Del</button>\r\n          <button class=\"mr-1\" :disabled=\" (Sel.length==2) ? false : true \" @click=\"onInsert\">Insert</button>\r\n          <button class=\"mr-1\" @click=\"onSave\">Save</button>\r\n          <button class=\"mr-1\" @click=\"onReset\">Reset</button>\r\n        </div>\r\n          <div id=\"stage-parent\">\r\n            <div id=\"container\" class=\"border\">\r\n                <v-stage ref=\"stage\"\r\n                    @click=\"onStageClick\"\r\n                    @mousemove=\"onStageMouseMove\" \r\n                    :config=\"configKonva\" >\r\n                    <v-layer ref=\"layer\">\r\n                        <v-circle :config=\"configCircle\"></v-circle>\r\n                        <v-rect ref=\"rect\" \r\n                            :config=\"{\r\n                                x: 100,\r\n                                y: 100,\r\n                                width: 150,\r\n                                height: 150,\r\n                                fill: 'yellow',\r\n                                stroke:'blue',\r\n                                strokeWidth:5,\r\n                                //shadowBlur: 10,\r\n                                draggable:true,\r\n                                visible:false\r\n                            }\"\r\n                        />\r\n                        <v-line ref=\"line\" :config=\"{\r\n                          points: [100,100,400,200],\r\n                          stroke: 'gray',\r\n                          strokeWidth: 5,\r\n                          lineCap: 'round',\r\n                          lineJoin: 'round',\r\n                          draggable:true,\r\n                          visible: false\r\n                        }\"></v-line>\r\n                        <v-shape ref=\"nice\"\r\n                          :config=\"{\r\n                            sceneFunc: function(context, shape) {\r\n                              let x=-20;\r\n                              let y=-50;\r\n                              context.beginPath();\r\n                              context.moveTo(x+20, y+50);\r\n                              context.lineTo(x+220, y+80);\r\n                              context.quadraticCurveTo(x+150, y+100, x+260, y+170);\r\n                              context.closePath();\r\n                              context.fillStrokeShape(shape);\r\n                            },\r\n                            fill: '#00D2FF',\r\n                            stroke: 'black',\r\n                            strokeWidth: 4,\r\n                            visible: true,\r\n                            draggable:true,\r\n                            visible:false,\r\n                          }\"\r\n                        />\r\n                        <v-shape ref=\"ball\"\r\n                          :config=\"{\r\n                            x:150,\r\n                            y:150,\r\n                            r:90,\r\n                            text:'1',\r\n                            fontSize:50,\r\n                            fill: 'aqua',\r\n                            stroke: 'black',\r\n                            textcolor:'red',\r\n                            strokeWidth: 5,\r\n                            draggable:true,  \r\n                            sel:false,      \r\n                            visible:false,\r\n                            sceneFunc: function (context, shape) {\r\n                              let fontSize=shape.getAttr('fontSize');//40;\r\n                              context.beginPath();\r\n                              context.arc(0, 0, shape.getAttr('r'), 0, 2 * Math.PI);\r\n                              context.fillStrokeShape(shape);\r\n                              let textcolor=shape.getAttr('textcolor');\r\n                              context.fillStyle=textcolor;//'white';\r\n                              context.textBaseline = 'middle'; \r\n                              context.textAlign = 'center';\r\n                              context.font=`${fontSize}px verdana`;//verdana//Times New Roman\r\n                              context.fillText(`${shape.getAttr('text')}`, 0,0);\r\n\r\n                            }\r\n                          }\"\r\n                        />\r\n                        <v-shape ref=\"board\" :config=\"{\r\n                          x:50,\r\n                          y:50,\r\n                          width:300,\r\n                          height:100,\r\n                          text:'Thức 101',\r\n                          fontSize:60,\r\n                          fill: 'yellow',\r\n                          stroke: 'red',\r\n                          strokeWidth: 1,\r\n                          draggable:true, \r\n                          visible:false,       \r\n                          sceneFunc: function (context, shape) {\r\n                            let fontSize=shape.getAttr('fontSize');\r\n                            context.beginPath();\r\n                            context.rect(0, 0, shape.getAttr('width'), shape.getAttr('height'));\r\n                            context.fillStrokeShape(shape);\r\n                            context.fillStyle='red';\r\n                            context.textBaseline = 'middle'; \r\n                            context.textAlign = 'center';\r\n                            context.font=`${fontSize}px Times New Roman`;\r\n                            context.fillText(`${shape.getAttr('text')}`, shape.getAttr('width')/2,shape.getAttr('height')/2);\r\n                          }\r\n                        }\">\r\n                        </v-shape>\r\n                    </v-layer>\r\n                </v-stage>\r\n            </div>\r\n          </div>\r\n\r\n      </b-col>\r\n      <b-col sm=\"9\">\r\n        {{msg}}{{hover}}\r\n        <div class=\"row no-gutters border-top p-1\">\r\n          <div v-for=\"(x,i) in listData\" :key=\"i\" class=\"mb-1 mr-1 col-sm-2\">\r\n            <div :class=\" (i==hover) ? 'border border-primary' : '' \" style=\"position:relative\" @mouseover=\" hover=i \" @mouseout=\" hover=-1 \" >\r\n              <img :src=\" x.src \" @click=\"onView(x.data)\" class=\"img-thumbnail\" >\r\n              <button @click=\"onDelData(i)\" class=\"m-1\" style=\"position:absolute; bottom:0; right: 0;\">Del</button>\r\n            </div>\r\n          </div>\r\n        </div>\r\n\r\n      </b-col>\r\n    </b-row>\r\n  </div>\r\n</template>\r\n<script>\r\nvar sceneWidth = 1500;\r\nvar sceneHeight = 1000;\r\nvar stage=null;\r\nvar layer=null;\r\nexport default {\r\n  name: 'Tpl',\r\n  components: {\r\n  },\r\n  computed: {\r\n  \ttest () {\r\n    \treturn 'hello'\r\n    },\r\n  },\r\n  data(){\r\n    return{\r\n        msg:\"hello\",\r\n        configKonva: {\r\n          width: sceneWidth,\r\n          height: sceneHeight\r\n        },\r\n        configCircle: {\r\n          x: sceneWidth/2,\r\n          y: sceneHeight/2,\r\n          radius: 90,\r\n          fill: \"yellow\",\r\n          stroke: \"black\",\r\n          strokeWidth: 5,\r\n          draggable:true,\r\n          visible:false,\r\n        },\r\n        count:0,\r\n        Sel:[],\r\n        arPoint:[],\r\n        listData:[],\r\n        hover:-1,\r\n        selColor:'',\r\n\r\n        p:{},\r\n        arSel:[],\r\n        unLink:false,\r\n    }\r\n  },\r\n  mounted(){\r\n    this.$nextTick(function(){\r\n      layer=this.$refs.layer.getNode();\r\n      stage=this.$refs.stage.getNode();\r\n      window.addEventListener(\"resize\", this.fitStageIntoParentContainer);\r\n      this.fitStageIntoParentContainer();\r\n    })\r\n  },\r\n  created(){\r\n  },\r\n  methods:{\r\n    onReset(){\r\n      this.arPoint.forEach((x,i)=>{\r\n        layer.find(`#${x.id}`)[0].destroy();\r\n      })\r\n      this.arPoint=[];\r\n      this.count=0;\r\n      this.draw();\r\n    },\r\n    onView(data){\r\n      this.arPoint.forEach((x,i)=>{\r\n        layer.find(`#${x.id}`)[0].destroy();\r\n      })\r\n      this.arPoint= JSON.parse(JSON.stringify(data.arPoint));\r\n      this.count=data.count;\r\n      this.arPoint.forEach((x,i)=>{\r\n        let ball=this.createBall(x.x,x.y,60,'myball',x.id.toString());\r\n        layer.add(ball);\r\n      })\r\n      this.draw();\r\n    },    \r\n    onDelData(i){\r\n      this.listData.splice(i,1);\r\n    },    \r\n    onSave(){\r\n      let src = stage.toDataURL();\r\n      let data={ \"arPoint\": this.arPoint, \"count\": this.count };\r\n      this.listData.push({src:src,data:JSON.parse(JSON.stringify(data))})//<--------------------cẩn thận Khi save data\r\n    },\r\n    onDel(){\r\n      let id=this.Sel[0];\r\n      layer.find(`#${id}`)[0].destroy();\r\n      let index=this.arPoint.findIndex(x=>x.id==id);\r\n      this.arPoint.splice(index,1);\r\n      this.draw();\r\n    },\r\n    insert(id1,id2){\r\n      let p1= layer.find(`#${id1}`)[0];\r\n      let x1= Math.floor(p1.x());\r\n      let y1= Math.floor(p1.y());\r\n      let p2= layer.find(`#${id2}`)[0];\r\n      let x2=Math.floor(p2.x());\r\n      let y2=Math.floor(p2.y());\r\n      let x= Math.floor((x1+x2)/2);//Math.abs(x1-x2)/2;\r\n      let y= Math.floor((y1+y2)/2);//Math.abs(y1-y2)/2;\r\n      let id=this.count;\r\n      this.count++;\r\n      let index1=this.arPoint.findIndex(x=>x.id==id1);\r\n      let index2=this.arPoint.findIndex(x=>x.id==id2);\r\n      let index=(index1 < index2) ? index1 : index2;\r\n      this.arPoint.splice(index+1,0,{id:id,x:x,y:y});\r\n      let ball=this.createBall(x,y,60,'myball',id.toString());\r\n      layer.add(ball);\r\n      this.draw();\r\n    },\r\n    onInsert(){\r\n      let id1=this.Sel[0];\r\n      let id2=this.Sel[1];\r\n      let index1=this.arPoint.findIndex(x=>x.id==id1);\r\n      let index2=this.arPoint.findIndex(x=>x.id==id2);\r\n      if(Math.abs( index1-index2)==1){\r\n        this.insert(id1,id2);\r\n      }else{\r\n        alert(\"can not insert\");\r\n      }\r\n    },\r\n    draw(){\r\n      if(this.getListLine().length==0){\r\n        let line=this.createLine(\"myline\");\r\n        layer.add(line);\r\n      }\r\n      let myline=this.getListLine()[0];\r\n      let lBall=this.getListBall();\r\n      let ar=[];\r\n      this.arPoint.forEach((x,i)=>{\r\n        let x1=x.x;\r\n        let y1=x.y;\r\n        ar.push(x1);\r\n        ar.push(y1);\r\n      })\r\n      myline.points(ar);\r\n      myline.moveToBottom();\r\n      this.msg={\r\n        arPoint:this.arPoint,\r\n        count:this.count,\r\n      }\r\n    },\r\n    createLine(name){\r\n      let line=this.$refs.line.getNode().clone();\r\n      line.visible(true);\r\n      line.name(name);\r\n      return line;\r\n    },\r\n    createBall(x,y,r,name,id){\r\n      let that=this;\r\n      let ball=this.$refs.ball.getNode().clone();\r\n      ball.visible(true);\r\n      ball.x(x);\r\n      ball.y(y);\r\n      ball.setAttr('r',r);\r\n      ball.fill(\"yellow\");\r\n      ball.stroke('black');\r\n      ball.name(name);\r\n      ball.id(id);\r\n      ball.setAttr(\"text\",id);\r\n      ball.on(\"click\",function(e){\r\n        let color=e.target.fill();\r\n        let colorFill;\r\n        let colorText;\r\n        if(color=='blue'){\r\n          colorFill='yellow';\r\n          colorText='red';\r\n        }else{\r\n          colorFill='blue';\r\n          colorText='white';\r\n        }\r\n        e.target.fill(colorFill);\r\n        e.target.setAttr('textcolor',colorText);\r\n        that.Sel=that.getSel();\r\n        that.draw();\r\n      });\r\n      ball.on('mouseover',function(e){\r\n        // that.selColor=e.target.fill();\r\n        // e.target.fill('red');\r\n      });\r\n      ball.on('mouseout',function(e){\r\n        // e.target.fill(that.selColor);\r\n      });\r\n      ball.on(\"dragend\",function(e){\r\n        let ball=e.target;\r\n        let id=ball.id();\r\n        let x=Math.floor(ball.x());\r\n        let y=Math.floor(ball.y());\r\n        let index=that.arPoint.findIndex(x=>x.id==id);\r\n        that.arPoint[index]={id:id,x:x,y:y};\r\n        that.draw();\r\n      });\r\n      ball.on(\"dragmove\",function(e){\r\n        let ball=e.target;\r\n        let id=ball.id();\r\n        let x=Math.floor(ball.x());\r\n        let y=Math.floor(ball.y());\r\n        let index=that.arPoint.findIndex(x=>x.id==id);\r\n        that.arPoint[index]={id:id,x:x,y:y};\r\n        that.draw();\r\n      });\r\n      return ball;\r\n    },\r\n    getSel(){\r\n      let kq=[];\r\n      let l=layer.find(\".myball\");\r\n      l.forEach((x,i)=>{\r\n        if(x.fill()=='blue'){\r\n          kq.push( parseInt (x.id()))\r\n        }\r\n      })\r\n      return kq;\r\n    },\r\n    onStageClick(e){\r\n      let chk=e.target.name();\r\n      if(chk=='myball'){\r\n        return;\r\n      }\r\n      let p=this.myGetMouse();\r\n      let ball=this.createBall(p.x,p.y,60,\"myball\",this.count.toString());\r\n      this.arPoint.push({id:this.count,x:p.x,y:p.y});\r\n      this.count++;\r\n      layer.add(ball);\r\n      this.draw();\r\n    },\r\n    onStageMouseMove(e){\r\n    },\r\n    getListRect(){ return layer.find(\".myrect\") },\r\n    getListLine(){ return layer.find(\".myline\") },\r\n    getListBall(){ return layer.find(\".myball\") },\r\n    myGetMouse() {\r\n      let scale=stage.scale().x;\r\n      var mousePos = stage.getPointerPosition();\r\n      var x = mousePos.x;\r\n      var y = mousePos.y;\r\n      var xx = Math.round(x / scale);\r\n      var yy = Math.round(y / scale);\r\n      return { x: xx, y: yy };\r\n    },\r\n    fitStageIntoParentContainer() {\r\n      var container = document.querySelector('#stage-parent');\r\n      var containerWidth = container.offsetWidth;\r\n      var scale = containerWidth / sceneWidth;\r\n      stage.width(sceneWidth * scale);\r\n      stage.height(sceneHeight * scale);\r\n      stage.scale({ x: scale, y: scale });\r\n      this.configKonva.width = sceneWidth * scale;\r\n      this.configKonva.height = sceneHeight * scale;\r\n    },\r\n  }\r\n}\r\n</script>\r\n<style scoped>\r\n</style>\r\n";